<template>
    <nacl-page-container no-header>
        <!-- <nacl-header
            transparent
            style="color: #fff"
        /> -->
        <nacl-page-content
            no-footer
            class="content-wrap"
        >
            <div>
                <div class="top-background">
                    <basic-title
                        color="light"
                        class="title"
                    >
                        购物车
                    </basic-title>
                    <span class="count">共{{ totalCount }}件宝贝</span>
                </div>

                <book-large-list
                    v-if="cartList.length"
                    class="list"
                    @change="onCartChange"
                    @delete="onCartRemove"
                >
                    <book-large-list-item
                        v-for="item in cartList"
                        :key="item.id"
                        :data="item"
                    />
                </book-large-list>
                <div v-else>
                    <div class="cart-no-data">
                        <img
                            src="../assets/cart-no-data.png"
                            width="100%"
                            class="cart-no-data-img"
                        >
                        <p class="no-data-text-1">空空如野~</p>
                    </div>

                    <!-- <div class="maybe-like">
                        <img
                            src="../assets/maybe-like.png"
                            alt=""
                        >
                    </div> -->
                    <p class="recommended">- 时尚专区 -</p>
                    <recommendedList />
                </div>
            </div>
        </nacl-page-content>
        <footer v-if="cartList.length">
            <nacl-flexbox
                class="footer-wrap"
                align="center"
                justify="between"
            >
                <span class="totals">¥{{ total }}</span>
                <a
                    class="buy-btn"
                    @click="handleBuy"
                >立即购买</a>
            </nacl-flexbox>
        </footer>
    <!-- <basic-footer v-else /> -->
    </nacl-page-container>
</template>

<script>
import BasicTitle from '../components/basic-title'
import BasicButton from '../components/basic-button'
import BookLargeList from '../components/book-large-list/index'
import recommendedList from '../components/recommended-list'
import { getCart } from '../services'

export default {
    name: 'cart',
    components: { recommendedList, BasicTitle, BasicButton, BookLargeList, BookLargeListItem: BookLargeList.Item },
    data () {
        return {
            chooseData: [],
            // total: 0,
            deleteTotal: 0,
            cartList: []
        }
    },
    computed: {
        // cartList () {
        //     console.log(getCart())
        //     return getCart()
        // },
        totalCount () {
            return this.cartList.length - this.deleteTotal
        },
        total () {
            let total = 0
            for (let i in this.cartList) {
                // console.log(i)
                const data = this.cartList[i]
                total += data.buyCount * data.price
            }
            return total
        }
    },
    async created () {
        this.cartList = await getCart()
    },
    mounted () {
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        }, 
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        onCartChange (data, total) {
            this.chooseData = data
            this.total = total
        },
        async onCartRemove (cartId) {
            this.cartList = await getCart()
        },
        handleBuy () {
            const checkedData = this.cartList.filter((item) => item.checked)
            if (checkedData.length === 0) {
                this.$toast('请勾选商品')
                return false
            }
            // this.$router.push(`/OrderDetail`)
            this.toGetUser();
            window.webkit.messageHandlers.orderDetail.postMessage('');
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
$footer-height: 58px * 2;
.recommended {
    padding-top: 30px;
    font-size: 32px;
    text-align: center;
    color: #4d83f7;
}
.content-wrap {
  padding-bottom: $footer-height;
}
.top-background {
  margin-top: -80px;
  padding: 90px 34px 38px;
  height: 328px;
  background-image: url(../assets/cart-bg.png);
  box-sizing: border-box;
  color: #ffffff;
  .title {
  }
  .count {
    font-size: 14px * 2;
  }
}
.list {
  margin-top: -120px;
}
.footer-wrap {
  z-index: 99;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  height: $footer-height;
  padding: 0 40px;
  box-sizing: border-box;
  .totals {
    font-size: 24px * 2;
    font-weight: 500;
    color: #d0021b;
  }
  .buy-btn {
    width: 124px * 2;
    height: 41px * 2;
    line-height: 41px * 2;
    background-color: #4081ff;
    color: #fff;
    font-size: 18px * 2;
    text-align: center;
    border-radius: 41px;
  }
}
.cart-no-data {
  position: relative;
}
.cart-no-data-img {
  display: block;
  margin: 20px auto 44px;
  width: 350px;
  height: 310px;
}
.no-data-text-1 {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  font-size: 28px;
  color: #666;
  text-align: center;
  letter-spacing: 0.3px;
}
.maybe-like {
  width: 80%;
  margin: auto;
  img {
    width: 100%;
  }
}
</style>
