<template>
    <div id="recommended-list">
        <commodity-list :list-data="listData" />
    </div>
</template>

<script>
import commodityList from '../commodity/commodity-list'
import { getGoodsByKeys } from '../../services'
// import axios from 'axios'
export default {
    name: 'recommended-list',
    components: {
        commodityList
    },
    props: {
    },
    data () {
        return {
            keysArray: ['T恤', '帆布鞋', '包包', '公仔', '针织衫', '皮衣'],
            listData: []
        }
    },
    created () {
        this.getList()
    },
    methods: {
        async getList () {
            const params = {
                pageIndex: this.pageIndex,
                pageSize: 20,
                keyword: this.keys
            }
            let listData = await getGoodsByKeys(params)
            this.listData = this.listData.concat(listData)
            if (listData.length === 0) {
                this.noData = true
            }
            this.busy = false
        }
    }
}
</script>

